@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
body {
  margin: 0;
  padding:0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  overflow-y: hidden;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.dyte{
  width:100vw;
  height:100vh;
  background:rgb(3, 1, 19);
  display:flex;
  flex-direction:row;
}
.icon{
  height:2.5vh;
  width:auto;
  margin:0 2vh;
}
.icon-closed{
  height:2.5vh;
  width:auto;
  margin:0;
}
.active{
  background:rgb(19, 20, 32);
}
::-webkit-scrollbar{
  background:rgb(26, 29, 43);
  width:8px;
  height:8px;
}
::-webkit-scrollbar-thumb{
  background:rgb(74, 71, 83);
  /* border-radius:7px; */
}
.ex-container{
    display:flex;
    flex-direction:column;
    color:white;
    border-right: 2px solid rgb(19, 20, 32);
    min-width:250px !important;
}
.ex-container-small{
    display:flex;
    flex-direction:column;
    color:white;
    border-right: 2px solid rgb(19, 20, 32);
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
}
.ex-header{
    background:rgb(19, 20, 32);
    display:flex;
    color:rgb(103, 102, 116);
    flex-direction:row;
    align-items:center;
    font-weight: 600;
    margin-bottom:3vh;
    justify-content:space-between;
    padding:2vh;
}
.ex-header span{
    cursor:pointer;
}
.ex-files{
    padding:2vh;
    cursor:pointer;
    display:flex;
    flex-direction:row;
    font-size:14px;
    align-items:center;
    justify-content:flex-start;
    color:white;
}
.ex-files a{
    display:flex;
    flex-direction:row;
    align-items:center;
    color:white;
    text-decoration: none;
}
.ex-files:hover{
   background:rgb(15, 16, 24);
}
.button{
    padding:2vh;
    text-align: center;
    font-weight:600;
    cursor:pointer;
    background: linear-gradient(289deg, rgba(238,217,149,1) 0%, rgba(231,133,176,1) 38%, rgba(124,181,255,1) 91%);
}
@media screen and (max-width:900px){
    .ex-container{
      position:absolute;
      z-index:100;
      height:100vh;
      background:rgb(3, 1, 19);
      box-shadow:3px 0 5px rgba(4, 14, 22, 0.37);
    }
}
.op-container{
    margin-top:3vh;
    display:flex;
    flex-direction:column;
    border-top:solid 2px;
    border-top: 2px solid rgb(19, 20, 32);
    padding:2vh;
    font-weight:600;
    flex-grow:1;
    border-bottom:solid 2px rgb(103, 102, 116);
    color:rgb(103, 102, 116);
}
.op-actions{
    color:rgb(103, 102, 116);
    font-size:14px;
    margin:2vh 0;
    display:flex;
    flex-direction:row;
    align-items:center;
    justify-content: flex-start;
    cursor:pointer;
    font-weight:600;
}
.op-header{
    margin-bottom:3vh;
}
.popup{
    height:100vh;
    width:100vw;
    z-index:300;
    background:rgba(0, 0, 0, 0.329);
    position:absolute;
    top:0;
    left:0;
    display:flex;
    align-items:center;
    justify-content:center;
}
.popup-container{
    padding:2vh;
    height:7vh;
    width:40vw;
    border-radius:5px;
    cursor:pointer;
    background:rgb(32, 31, 36);
    display:flex;
    flex-direction:row;
    align-items:center;
    justify-content:center;
}
.popup-text{
    flex-grow:1;
    padding:2vh;
    border-radius:5px;
    background:rgb(51, 50, 56);
    color:rgb(157, 156, 167);
    /* margin-right:2vh; */
}
.popup-container span{
    padding:2vh;
    background:rgb(51, 50, 56);
    border-radius:5px;
    margin-left:2vh;
}
.popup-container .active{
    padding:2vh;
    background:rgb(78, 76, 88);
    color:black;
    border-radius:5px;
    margin-left:2vh;
}
.link-popup-container{
    display:flex;
    flex-direction:row;
    align-items:flex-start;
    width:80vw;
    height:80vh;
    background:rgb(32, 31, 36);
    border-radius:7px;
    justify-content:space-between;
    overflow: hidden;
}
.link-item{
    padding:2vh;
    margin:1vh 0;
    margin-right:3vh;
    background:rgb(51, 50, 56);
    border-radius:5px;
    cursor:pointer;
}
.links-container{
    max-width:300px;
    margin:1vh 2vh;
    display:flex;
    height:60vh;
    flex-direction:column;
    overflow-y: scroll;
}
.view-demo{
    border:none;
    height:76vh;
    overflow: scroll;
    width:90%;
    margin:2vh;
    outline:none;
    background:white;
    border-radius:5px;
}
.close-popup{
    margin-left:2vh;
    margin-top:1vh;
    cursor:pointer;
}
.link-viewer{
    display:flex;
    flex-direction:column;
    align-items:center;
    flex-grow:1;
}
.button-link{
    margin:2vh;
    padding:2vh;
    width:300px;
    outline:none;
    border:none;
    cursor:pointer;
    font-weight:600;
    color:white;
    z-index:200;
    background: linear-gradient(289deg, rgba(238,217,149,1) 0%, rgba(231,133,176,1) 38%, rgba(124,181,255,1) 91%);
    border-radius:3px;
}
.ed-container{
    display:flex;
    flex-direction:column;
    height:100vh;
    background:rgb(3, 1, 19);
}
.ed-header{
    border-bottom:solid 3px rgb(39, 41, 63);
    background:rgb(3, 1, 19);
}
.ed-head-title{
    padding:1vh 0;
    width:200px;
    border-right:solid 3px rgb(39, 41, 63);
    height:3vh;
    color:white;
    display:flex;
    flex-direction:row;
    font-style: italic;
    font-size:14px;
    cursor:pointer;
}
.ed-editor-body{
    overflow-y:auto;
    overflow-x: hidden;
    height:100vh;
    resize: horizontal;
    min-width:20vw;
    width:40vw;
    max-width:60vw !important;
    color:rgb(223, 223, 223);
    font-family:monospace;
    font-size:15px;
    margin:0;
    padding:0;
}
.ed-editor-body p{
    padding:1vh;
    margin:0;
}
.welcome-box{
    flex-grow:1;
    height:88vh;
    margin:1vh;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
}
.welcome-box ul{
    color:rgb(99, 94, 112);
    font-weight:600;
    font-style: italic;
}
.welcome-box h1{
   margin:1vh 0;
}
.welcome-box h3{
    margin:1vh 0;
 }
.cv-container{
    display:flex;
    flex-direction:column;
    flex-grow:1;
    background:rgb(255, 255, 255);
}
.cv-header{
    display:flex;
    padding:1vh;
    color:white;
    font-weight:600;
    color:rgb(150, 149, 160);
    background:rgb(42, 42, 48);

}
.cv-viewer{
    margin:0;
    flex-grow: 1;
    border:none;
}
